import React, { useState } from 'react';
import { toast } from 'react-toastify';
import { useNavigate, Link } from 'react-router-dom';
import { Box, TextField, InputAdornment, Divider, Button, Grid, Typography, useMediaQuery, Stack } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import { Lock as IconLock } from '@mui/icons-material';
import AuthCode, { AuthCodeRef, AuthCodeProps } from 'react-auth-code-input';
import AuthWrapper from '../AuthWrapper';
import AuthCardWrapper from '../AuthCardWrapper';
import AuthFooter from 'ui-component/cards/AuthFooter';
import { apiCheckEmailVerificationCode } from '../../../actions/user';
import LogoImg from '../../../assets/images/logo.png';
import AnimateButton from 'ui-component/extended/AnimateButton';

const VerifyCodeForm = () => {
  const theme = useTheme();
  const matchDownSM = useMediaQuery(theme.breakpoints.down('md'));
  const navigate = useNavigate();
  const [verifyCode, setVerifyCode] = useState();
  const handleOnChange = (res) => {
    setVerifyCode(res);
  };
  const sendEmailFunc = () => {
    if (!verifyCode) toast.error('F체gen Sie den VerifyCode ein');
    else {
      apiCheckEmailVerificationCode({ code: verifyCode })
        .then((res) => {
          if (res.status === 200) {
            toast.success('Wir senden das ge채nderte Passwort an Ihre E-Mail.');
            navigate('/auth/password-reset');
          }
        })
        .catch((err) => {});
    }
  };

  return (
    <AuthWrapper>
      <Grid container display="flex" direction="column" justifyContent="flex-end" sx={{ minHeight: '100vh' }}>
        <Grid item xs={12}>
          <Grid container display="flex" justifyContent="center" alignItems="center" sx={{ minHeight: 'calc(100vh - 68px)' }}>
            <Grid item sx={{ m: { xs: 1, sm: 3 }, mb: 0 }}>
              <AuthCardWrapper>
                <Grid container spacing={2} alignItems="center" justifyContent="center">
                  <Grid item sx={{ mb: 3 }}>
                    <Link to="#">
                      <img src={LogoImg} alt="" style={{ width: '20%' }} />
                    </Link>
                  </Grid>
                  {/* <Grid item xs={12}>
                    <Grid container direction={matchDownSM ? 'column-reverse' : 'row'} alignItems="center" justifyContent="center">
                      <Grid item>
                        <Stack alignItems="center" justifyContent="center" spacing={1}>
                          <Typography color={theme.palette.success.green} gutterBottom variant={matchDownSM ? 'h3' : 'h2'}>
                            Passwort vergessen?
                          </Typography>
                        </Stack>
                      </Grid>
                    </Grid>
                  </Grid> */}
                  <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'center' }}>
                    {/* <TextField
                      fullWidth
                      name="verifycode"
                      label="VerifyCode"
                      type="number"
                      variant="outlined"
                      margin="normal"
                      value={verifyCode}
                      onChange={(e) => setVerifyCode(e.target.value)}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <IconLock />
                          </InputAdornment>
                        )
                      }}
                    /> */}
                    <AuthCode allowedCharacters="numeric" onChange={handleOnChange} containerClassName="container-auth-code" length={5} />
                  </Grid>

                  <Box sx={{ mt: 4 }}>
                    <AnimateButton>
                      <Button
                        fullWidth
                        size="large"
                        type="submit"
                        variant="contained"
                        sx={{ backgroundColor: theme.palette.success.green }}
                        onClick={sendEmailFunc}
                      >
                        Code 체berpr체fen
                      </Button>
                    </AnimateButton>
                  </Box>
                  <Grid item xs={12}>
                    <Divider />
                  </Grid>
                  <Grid item xs={12}>
                    <Grid item container direction="column" alignItems="center" xs={12}>
                      <Typography component={Link} to="/auth/signup" variant="subtitle1" sx={{ textDecoration: 'none' }}>
                        Sie haben kein Konto?
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              </AuthCardWrapper>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} sx={{ m: 3, mt: 1 }}>
          <AuthFooter />
        </Grid>
      </Grid>
    </AuthWrapper>
  );
};

export default VerifyCodeForm;
